import { useState } from 'react';
import End from "@/components/End";
import Image from 'next/image';
import Footer from "@/components/Footer";
import Photo from "@/components/Photo";
import Teams from "@/components/Teams";
import WebTeam from "@/components/WebTeam";
import BowAndArrow from "@/components/bow-arrow";
import BoxDesign from "@/components/box";
import HindiEvents from "@/components/hindiEvents";
import RangoliDesign from "@/components/rangoli";
import ComingSoon from "@/components/soon";
import styles from './page.module.css';

export default function Home() {
  const [hoveredBox, setHoveredBox] = useState(null);

  const events = [
    {
      id: 1,
      pattern: "/image.png",
      altText: "Kavyanjali Event",
      title: "Kavyanjali 2.0",
      description: "A celebration of poetry and literature"
    },
    {
      id: 2,
      pattern: "/p2.png",
      altText: "Rangotsav Event",
      title: "Rangotsav",
      description: "Festival of colors and creativity"
    },
    {
      id: 3,
      pattern: "/p3.png",
      altText: "Hindi Diwas Event",
      title: "Hindi Diwas",
      description: "Celebrating the Hindi language"
    }
  ];
  return (
    <>
      <div className={styles.section}>
        <div className="relative w-full min-h-screen overflow-hidden">
          <div className="container mx-auto px-4 py-10 max-w-5xl relative">
            {/* Hindi Events Header */}
            <div className="relative mx-auto w-fit px-8 py-6 text-black group z-10">
              <div className="absolute -top-8 -left-8 w-24 h-24 opacity-20 rotate-45 bg-orange-500 rounded-full 
                transition-all duration-500 group-hover:rotate-90 group-hover:scale-110" 
                style={{
                  backgroundImage: `radial-gradient(circle at center, transparent 30%, currentColor 30%, currentColor 40%, 
                  transparent 40%, transparent 60%, currentColor 60%, currentColor 70%, transparent 70%)`
                }}
              />
              <div className="absolute -bottom-8 -right-8 w-24 h-24 opacity-20 -rotate-45 bg-orange-500 rounded-full 
                transition-all duration-500 group-hover:-rotate-90 group-hover:scale-110"
                style={{
                  backgroundImage: `radial-gradient(circle at center, transparent 30%, currentColor 30%, currentColor 40%, 
                  transparent 40%, transparent 60%, currentColor 60%, currentColor 70%, transparent 70%)`
                }}
              />
              
              <div className="absolute inset-0 bg-gradient-to-r from-orange-200 to-orange-300 rotate-1 rounded-lg 
                transition-all duration-500 group-hover:rotate-2 group-hover:scale-105" />
              <div className="absolute inset-0 bg-gradient-to-l from-orange-100 to-transparent -rotate-1 rounded-lg 
                transition-all duration-500 group-hover:-rotate-2 group-hover:scale-105" />
              
              <div className="relative px-8 py-6 rounded-lg bg-gradient-to-r from-[#e1745d] to-[#ff8f7d] 
                shadow-lg transition-all duration-500 group-hover:shadow-2xl">
                <div className="absolute inset-0 rounded-lg border-2 border-orange-600/30 
                  transition-all duration-500 group-hover:border-orange-600/50" />
                
                <div className="relative">
                  <div className="absolute -left-6 -right-6 h-1 bg-orange-600/30 top-1/2 transform -translate-y-1/2
                    transition-all duration-500 group-hover:scale-x-110" />
                  
                  <h1 className="text-5xl md:text-6xl tracking-wider font-bold flex items-center justify-center gap-4"
                    style={{ fontFamily: 'Samarkan' }}>
                    <span className="text-red-700 text-4xl relative animate-pulse">
                      ●
                      <span className="absolute inset-0 animate-ping">●</span>
                    </span>
                    
                    <span className="relative text-orange-900 group-hover:text-orange-950 transition-colors duration-300">
                      Hindi Club Events
                      <span className="absolute -top-2 -right-4 text-2xl text-orange-600 animate-bounce">॥</span>
                      <span className="absolute -bottom-2 -left-4 text-2xl text-orange-600 animate-bounce delay-150">॥</span>
                    </span>
                  </h1>
                </div>
                
                <div className="absolute bottom-2 left-4 text-orange-600/50 text-xl">❈</div>
                <div className="absolute top-2 right-4 text-orange-600/50 text-xl">❈</div>
              </div>
            </div>

            {/* Event Boxes */}
            <div className="mt-10 space-y-4 relative z-10">
              {events.map((event) => (
                <div key={event.id} className="flex justify-center items-center">
                  <div 
                    className="flex justify-center items-center h-full mx-auto w-full max-w-xl mb-6 relative group"
                    onMouseEnter={() => setHoveredBox(event.id)}
                    onMouseLeave={() => setHoveredBox(null)}
                  >
                    <div className="absolute inset-0 bg-gradient-to-r from-orange-100 to-orange-200 rounded-lg rotate-3 
                      transition-all duration-300 group-hover:rotate-6 group-hover:scale-105" />
                    <div className="absolute inset-0 bg-gradient-to-l from-orange-50 to-transparent rounded-lg -rotate-2 
                      transition-all duration-300 group-hover:-rotate-4 group-hover:scale-105" />
                    
                    <div className="relative w-full bg-white rounded-lg overflow-hidden shadow-lg 
                      transition-all duration-300 group-hover:shadow-2xl">
                      <Image
                        src={event.pattern}
                        alt={event.altText}
                        width={800}
                        height={176}
                        className={`w-full h-44 object-cover transition-all duration-500 
                          ${hoveredBox === event.id ? 'scale-110' : 'scale-100'}`}
                      />
                      <div className="p-4">
                        <h3 className="text-2xl font-bold mb-2" style={{ fontFamily: 'Samarkan' }}>{event.title}</h3>
                        <p className="text-gray-600">{event.description}</p>
                      </div>
                    </div>
                  </div>
                </div>
              ))}
            </div>

            {/* Bow and Arrow - Changed from fixed to absolute positioning */}
            <div className="absolute top-20 right-20 w-56 transition-transform duration-300 
              hover:scale-125 cursor-pointer animate-[sweep_2s_ease-in-out_infinite] z-20">
              <Image
                src="/bow copy.png"
                alt="Bow and Arrow"
                width={224}
                height={224}
                className="w-full h-auto rounded-md"
              />
              <style>{`
                @keyframes sweep {
                  0% { transform: translateX(0); }
                  50% { transform: translateX(-20px); }
                  100% { transform: translateX(0); }
                }
              `}</style>
            </div>

            {/* Rangoli Designs */}
            <div className="absolute left-0 top-1/2 -translate-y-1/2 z-0">
              <Image
                src="/rangoli.webp"
                alt="Rangoli Design Left"
                width={160}
                height={160}
                className="rounded-full opacity-50"
              />
            </div>
            <div className="absolute right-0 top-1/2 -translate-y-1/2 z-0">
              <Image
                src="/rangoli.webp"
                alt="Rangoli Design Right"
                width={160}
                height={160}
                className="rounded-full opacity-50"
              />
            </div>

            {/* Coming Soon - Adjusted positioning */}
            <div className="absolute left-4 top-1/2 -translate-y-1/2 rotate-90 scale-y-[-1] scale-x-[-1] 
              hover:scale-105 transition-all duration-300 z-10">
              <div
                className="p-4 rounded-lg shadow-lg hover:shadow-2xl transition-shadow duration-300"
                style={{
                  background: "linear-gradient(90deg, #D9D9D9 0%, #FF8B50 100%)",
                }}
              >
                <p 
                  className="text-7xl font-serif font-bold tracking-wider text-black"
                  style={{ fontFamily: 'Samarkan' }}
                >
                  Coming Soon
                </p>
              </div>
            </div>
          </div>
        </div>
      </div>

      <div className={styles.section}>
        <Photo />
      </div>
      <div className={styles.section}>
        <Teams />
      </div>
      <div className={styles.section}>
        <End />
      </div>
      <div className={styles.section}>
        <WebTeam />
      </div>
      <div className={styles.section}>
        <Footer />
      </div>
    </>
  );
}
